
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Masker from 'vux/src/components/masker/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
components: {
ViewBox,
XHeader,
Masker,
Tab,
TabItem,
Scroller
},
data () {
return {
index: 0,
status: {
pullupStatus: 'default'
},
isForOther: true,
myList: [],
othersList: [],
pulldownConfig: {
content: '',
height: 60,
autoRefresh: true,
downContent: '释放刷新',
upContent: '',
loadingContent: '加载中...',
clsPrefix: 'xs-plugin-pulldown-'
}
}
},
mounted () {


this.onPulldownLoading()
this.onPulldownLoading()
this.$nextTick(() => {

this.$refs.guaranteeScroller.reset({
top: 0
})
})
},
methods: {
handler (flag) {
if (flag === 0) {
this.isForOther = true
this.guaranteeForOther()
} else {
this.isForOther = false
this.guaranteeForMe()
}
},
refresh () {
if (this.isForOther) {
this.guaranteeForOther()
} else {
this.guaranteeForMe()
}
},
toGuarantee (status, applyNo) {
if (status === '201') {

this.$router.push({path: '/handleGuarantee', query: {applyNo: applyNo}})
} else if (status === '203') {
this.$router.push({path: '/GuaranteeSign', query: {applyNo: applyNo}})
} else if (status === '204') {
this.$vux.toast.text('当前状态无法查看详情')
}
},
onPulldownLoading () {
let self = this
self.isForOther ? self.guaranteeForOther() : self.guaranteeForMe()
self.$nextTick(() => {

self.$refs.guaranteeScroller.donePulldown()
self.$refs.guaranteeScroller.reset({
top: 0
})
})
},
guaranteeForOther () {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/assureForOthers.do'
let data = {
action: 'init_request',
path: path,
params: {
token: this.$store.state.userInfo.token
}
}

self.$store.dispatch('normalRequest', data).then(data => {
if (data.response === 'success') {
data.data.myList.length === 0 ? self.color = '#fff' : self.color = ''
self.myList = data.data.myList
} else {
this.$vux.toast.text(data.data)
}
self.$refs.guaranteeScroller.reset({
top: 0
})
})
},
guaranteeForMe () {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/assureForMe.do'
let data = {
action: 'init_request',
path: path,
params: {
token: this.$store.state.userInfo.token
}
}

self.$store.dispatch('normalRequest', data).then(data => {
if (data.response === 'success') {
data.data.othersList.length === 0 ? self.color = '#fff' : self.color = ''
self.othersList = data.data.othersList
} else {
this.$vux.toast.text(data.data)
}
self.$refs.guaranteeScroller.reset({
top: 0
})

})
}
}
}



